.tooltip {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;

  row-gap: 30px;
}
.img {
  width: 175px;
  height: 175px;
  object-fit: contain;
}
.text {
  font-size: 26px;
  font-weight: 700;
  color: var(--text-color);
  line-height: 140%;
  text-align: center;
}
